import { Input } from "./input";

export interface Result {
  x: {
    x: {
      mean: number;
    };
  };
  y: {
    x: {
      mean: number;
    };
  };
  theta: {
    x: number;
  };
}

export default async function fetchResult(
  input: Input
): Promise<Array<Result>> {
  const wasm = await import("wasm");
  const mazeWidth = Math.floor(input.maze_string.split("\n").length / 2);

  const simulator = (() => {
    switch (mazeWidth) {
      case 4:
        return wasm.Simulator4.new(JSON.stringify(input));
      case 8:
        return wasm.Simulator8.new(JSON.stringify(input));
      case 16:
        return wasm.Simulator16.new(JSON.stringify(input));
      case 32:
        return wasm.Simulator32.new(JSON.stringify(input));
      default:
        throw Error(`Invalid mazeWidth: ${mazeWidth}`);
    }
  })();

  let results: Array<Result> = [];
  for (;;) {
    try {
      await new Promise<void>((resolve, rejected) => {
        setTimeout(() => {
          const batchResults: Array<Result> = [];
          try {
            for (let i = 0; i < 300; i++) {
              batchResults.push(JSON.parse(simulator.simulate_one_step()));
            }
            results = results.concat(batchResults);
            if (results.length > input.time_limit) {
              rejected("time limit exceeded");
            }
          } catch (err) {
            results = results.concat(batchResults);
            rejected(err);
          }
          resolve();
        });
      });
    } catch (err) {
      console.log(err);
      break;
    }
  }
  return results;
}
