export type Direction =
  | "North"
  | "NorthEast"
  | "East"
  | "SouthEast"
  | "South"
  | "SouthWest"
  | "West"
  | "NorthWest";

export interface Input {
  config: {
    start: {
      x: number;
      y: number;
      direction: Direction;
    };
    return_goal: {
      x: number;
      y: number;
    };
    goal: {
      x: number;
      y: number;
    };
    search_initial_route: "Init";
    search_final_route: "Final";
    estimator_cut_off_frequency: number;
    period: number;
    translational_parameters: {
      kp: number;
      ki: number;
      kd: number;
      model_k: number;
      model_t1: number;
    };
    rotational_parameters: {
      kp: number;
      ki: number;
      kd: number;
      model_k: number;
      model_t1: number;
    };
    tracker_gain: number;
    tracker_dgain: number;
    valid_control_lower_bound: number;
    low_zeta: number;
    low_b: number;
    fail_safe_voltage_threshold: number;
    search_velocity: number;
    max_velocity: number;
    max_acceleration: number;
    max_jerk: number;
    spin_angular_velocity: number;
    spin_angular_acceleration: number;
    spin_angular_jerk: number;
    run_slalom_velocity: number;
    slip_angle_const: number;
    estimator_approximation_threshold: number;
    estimator_standard_deviation_delta: number;
  };
  state: {
    current_node: {
      x: number;
      y: number;
      direction: Direction;
    };
    robot_state: {
      x: {
        x: {
          mean: number;
          standard_deviation: number;
        };
        v: number;
        a: number;
        j: number;
      };
      y: {
        x: {
          mean: number;
          standard_deviation: number;
        };
        v: number;
        a: number;
        j: number;
      };
      theta: {
        x: number;
        v: number;
        a: number;
        j: number;
      };
    };
  };
  maze_string: string;
  time_limit: number;
}

